
import {inject, onMounted, reactive, watch} from "vue"
import DialogModal from "@/components/DialogModal.vue"
import {API_URL} from "@/config"
import { useRoute } from 'vue-router'
import queryString from 'query-string'
export default {
  name: "RouteHome",
  components:{
    DialogModal,
  },
  
    setup() {
      const axios: any = inject('axios')
      const dayjs: any = inject('dayjs')
      const alertMessage: any = inject('alert-message')
      const state = reactive({
        loading: false,
        data:[] as any[],
        nowUnix: dayjs().unix(),
        delete: {
          submitting: false,
          route: undefined as any,
        },
      })
      const route = useRoute()




      function typeString(t: number) {
        switch (t || 0) {
          case 1:
            return "ICMP"
          case 100:
            return "UDP"
          case 101:
            return "TCP"
          default:
            return "NONE"
        }
      }

      async function onDelete() {
        let route = state.delete.route
        state.delete.submitting = true
        try {
          let res = await axios.delete(API_URL + "/api/route/" + route.id)
          if (res.data.error) {
            throw new Error(res.data.error)
          }
          let newData = []
          for (const key in state.data) {
            let item = state.data[key]
            if (item.id !== route.id) {
              newData.push(item)
            }
          }
          state.data = newData
          state.delete.route = undefined
        } catch (e: any) {
            alertMessage(e?.response?.data?.error || e)
        } finally {
          state.delete.submitting = false
        }
      }

      watch(() => route.query, onList)

      async function onList() {
        if (route.name !== "route/home") {
          return
        }
        state.loading = true
        try {
          let res = await axios.get(API_URL + "/api/route?" +  queryString.stringify(route.query))
          if (res.data.error) {
            throw new Error(res.data.error)
          }
          state.data = res.data.data
        } catch (e) {
          alertMessage(e)
        } finally {
          state.loading = false
        }
      };
      
      onMounted(onList)
      
    return {
      onList,
      state,
      onDelete,
      typeString,
      API_URL,
    }
  },
}
